:host {
  display: block;
}

:host.past .heading {
  text-align: center;
  margin-block-start: 89px;
}
:host.past .heading .meta {
  margin-block-end: var(--space-5);
}
:host.past .heading .ui-heading-1 {
  margin-block-end: var(--space-5);
  color: #010610;
}
:host.past .heading .hosts {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-block-end: var(--space-9);
}
:host.past .heading .hosts .host {
  display: flex;
}
:host.past .heading .hosts .host + .host {
  margin-block-start: var(--space-3);
}
:host.past .heading .hosts .host .avatar {
  border-radius: var(--radius-4);
  margin-inline-end: 11px;
  width: 28px;
  height: 28px;
}
:host.past .heading .hosts .host .description {
  color: #73849A;
}
:host.past .video {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-block-end: var(--space-11);
}
:host.past .video.blured wistia-video {
  filter: blur(16px);
}
:host.past .video .overlay-form {
  border-radius: var(--radius-2);
  text-align: center;
  position: absolute;
  z-index: 1000;
  background: white;
  padding: 48px;
}
:host.past .video .overlay-form .heading-group .ui-heading {
  margin-block-end: var(--space-3);
}
:host.past .article {
  margin-block-end: var(--space-11);
  max-width: 672px;
  margin-inline-start: auto;
  margin-inline-end: auto;
}

:host.future .landing-image {
  background: var(--c-lavender-70);
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  object-fit: cover;
}
:host.future .heading-group {
  padding-block-start: var(--space-11);
  padding-block-end: var(--space-11);
  position: relative;
  min-height: 512px;
  display: flex;
  align-items: center;
}
:host.future .heading-group .ui-heading-1 {
  max-width: 704px;
  color: white;
}
:host.future .heading-group .when {
  margin-block-start: var(--space-5);
  color: white;
}
:host.future .heading-group .meta {
  margin-block-end: var(--space-5);
}
:host.future .heading-group .meta .type {
  color: white;
}
:host.future .heading-group .register-button {
  background: white;
  color: #5061F2;
  margin-block-start: 64px;
}
:host.future .wrapper {
  display: flex;
  margin-block-start: var(--space-11);
  margin-block-end: var(--space-11);
}
:host.future .wrapper > * {
  flex-grow: 1;
}
:host.future .wrapper .article {
  max-width: 576px;
}
:host.future .wrapper .article .register-button {
  margin-block-start: var(--space-6);
  background: #3880FF;
  color: white;
}
@media screen and (max-width: 1023px) {
  :host.future .wrapper {
    flex-direction: column;
  }
  :host.future .wrapper .hosts {
    margin-block-start: var(--space-11);
    margin-inline-start: 0;
  }
}
:host.future .hosts {
  margin-inline-start: var(--space-11);
}
:host.future .hosts .ui-heading {
  margin-block-end: 40px;
}
:host.future .hosts resource-author-item + resource-author-item {
  margin-block-start: var(--space-3);
}
:host.future resource-meta {
  --tag-color: rgba(255, 255, 255, .8);
}

.meta {
  display: inline-flex;
}
.meta .type {
  margin-inline-end: 17px;
}

.article .ui-heading-2 {
  margin-block-end: var(--space-3);
}
.article p, .article li {
  color: #445B78;
}
.article p + p, .article p + ul, .article ul + p, .article ul + ul {
  margin-block-start: var(--space-5);
}
.article ul {
  margin-inline-start: 38px;
}
.article li {
  position: relative;
}
.article li + li {
  margin-block-start: var(--space-5);
}
.article li::before {
  left: -38px;
  top: 4px;
  position: absolute;
  content: "";
  background: var(--checkmark-path);
  height: 20px;
  width: 20px;
}

.register-button {
  padding: 16px 20px;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.1em;
  border-radius: 8px;
}

site-modal .title {
  text-align: center;
}
site-modal .title .ui-heading {
  margin-block-end: var(--space-3);
}